#about{
    background-color: var(--color-bg-variant);
    padding-bottom: 10%;
    padding-top: 5%;
}

.about__container{
    display: grid;
    grid-template-columns: 35% 10 0%;
    gap: 15%;
    
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-bg-variant),
        transparent
    );
    display: grid;
    place-items: center;
    
}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0)
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}
.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5{
    font-size: 0.9rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

.modal{
    display: none; /*hidden by default*/
    position: fixed; /*Stay in place*/
    z-index: 1; /*Sit on top*/
    left: 0;
    top: 0;
    width: 100%; /*Full width*/
    height: 100%; /*Full height*/
    overflow: auto; /*Enable scroll if needed*/
    background-color: rgba(0, 0, 0); /*Fallback color*/
    background-color: #ffffff66; /*Black w/opacity*/
    transition: 0.5s;
}

/* Modal Content/Box */
.modal-content{
    background-color: #111111b7;
    margin-left: 25%; /*15% from the top and centered*/
    padding: 20px;
    border: none;
    width: 50%; /*Could be more or less, depending on screen size*/
    padding: 50px 40px 50px 40px; /* top - left - bottom - right */
    color: #ffffff;
    
}
 
/* ================== MEDIA QUERIES (MEDIUM DEVICES) =============*/
@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap:0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about-content p{
        margin: 1rem 0 1.5rem;
    }
}

/*=================== MEDIA QUERIES (SMALL DEVICES) ==============*/
@media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards{
        grid-template-columns:  1fr 1fr;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }
}